import React, { useEffect } from 'react';
import * as orderActions from '../../redux/orders/order.actions';
import * as orederReducer from '../../redux/orders/order.reducer';
import {useDispatch, useSelector} from 'react-redux';
import Spinner from '../layout/Spinner';

interface IProps{}
interface IState{
    orderKey : orederReducer.OrderState;
}

let OrderList : React.FC<IProps> = ({}) => {
    let dispatch = useDispatch();

    //get orders info from Store
    let orderState : orederReducer.OrderState = useSelector((store : IState) => {
        return store.orderKey
    });

    let {orders, loading} = orderState;

    useEffect(() => {
        dispatch(orderActions.getAllOrders());

    }, []);

    return(
        <React.Fragment>
            <section className = "mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Your Orders</p>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corrupti iste pariatur minima eum, ducimus rerum eos assumenda dolores dicta mollitia consequatur sequi amet libero quia delectus earum temporibus! Id, exercitationem?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        orders && orders.length > 0 &&
                        <div className="container mt-3">
                        <div className="row">
                            <div className="col">
                                <table className="table table-hover table-striped bg-brains text-center shadow-lg">
                                    <thead className="bg-dark text-white">
                                    <tr>
                                        <th>Order ID</th>
                                        <th>NAME</th>
                                        <th>EMAIL</th>
                                        <th>MOBILE</th>
                                        <th>ITEMS</th>
                                        <th>TOTAL</th>
                                        <th>DATE</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    {
                                        orders.length > 0 ? <React.Fragment>
                                            {
                                                orders.map(order => {
                                                    return (
                                                        <tr key={order._id}>
                                                            <td>{order._id.substr(order._id.length - 5)}</td>
                                                            <td>{order.name}</td>
                                                            <td>{order.email}</td>
                                                            <td>{order.phone}</td>
                                                            <td>
                                                                <ul className="list-group">
                                                                    {
                                                                        order.items.map(item => {
                                                                            return (
                                                                                <React.Fragment>
                                                                                    <li className="list-group-item">
                                                                                        NAME : {item.name} {" "}
                                                                                        Qty : {item.qty} {" "}
                                                                                        Price : {item.price} {" "}
                                                                                    </li>
                                                                                </React.Fragment>
                                                                            )
                                                                        })
                                                                    }
                                                                </ul>
                                                            </td>
                                                            <td>&#8377;{(Number(order.total) + Number(order.tax)).toFixed(2)}</td>
                                                            <td>{new Date(order.createdAt).toLocaleDateString()}</td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </React.Fragment> : <React.Fragment>
                                            <div>
                                                NO ORDERS FOUND
                                            </div>
                                        </React.Fragment>
                                    }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    }
                    </React.Fragment>
            }
        </React.Fragment>
    )
};

export default OrderList;