import React from 'react';
import {NavLink} from 'react-router-dom';
import * as orderActions from '../../redux/orders/order.actions';
import * as orderReducer from '../../redux/orders/order.reducer';
import {useDispatch, useSelector} from 'react-redux';

const PRODUCT_TAX : number = 5.0;

interface IProps{}
interface IState{
    orderKey : orderReducer.OrderState
}

let CartPage : React.FC<IProps> = ({}) => {
    let dispatch = useDispatch();

    //get the cart information from redux store
    let cartState : orderReducer.OrderState = useSelector((store : IState) => {
        return store.orderKey;
    });

    let {cartItems} = cartState;

    let calcTotal = () => {
        let total = 0;
        for(let cartItem of cartItems){
            total += Number(cartItem.price) * Number(cartItem.qty);
        }
        return total;
    };

    let calcTax = ():number => {
        return calcTotal() * PRODUCT_TAX / 100
    };

    let calcGrandTotal = ():number => {
        return calcTotal() + calcTax();
    }

    let clickIncrQty = (productId : string) => {
        dispatch(orderActions.incrProductQty(productId))
    };

    let clickDecrQty = (productId : string) => {
        dispatch(orderActions.decrProductQty(productId))
    };

    let clickDeleteProduct = (productId : string) => {
        dispatch(orderActions.deleteCartItem(productId))
    };

    return(
        <React.Fragment>
            <section className = "mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Your Cart</p>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corrupti iste pariatur minima eum, ducimus rerum eos assumenda dolores dicta mollitia consequatur sequi amet libero quia delectus earum temporibus! Id, exercitationem?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                cartItems.length > 0 ? <React.Fragment>
                    <section className = "mt-3">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-8">
                                    <div className="card">
                                        <div className="card-header bg-dark text-white">
                                            <p className="h4">Your Cart Items</p>
                                        </div>
                                        <div className="card-body">
                                            <table className="table table-striped table-hover text-center table-success">
                                                <thead>
                                                    <tr>
                                                        <th>Image</th>
                                                        <th>Name</th>
                                                        <th>Price</th>
                                                        <th>Qty</th>
                                                        <th>Total</th>
                                                        <th>Action</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {
                                                        cartItems.map(cartItem => {
                                                            return(
                                                                <tr key = {cartItem._id}>
                                                                    <td>
                                                                        <img src= {cartItem.image} alt="" width = {50} height = {80} />
                                                                    </td>
                                                                    <td>{cartItem.name}</td>
                                                                   <td>&#163; {Number(cartItem.price).toFixed(2)}</td>
                                                                   <td>
                                                                       <i onClick = {clickDecrQty.bind(this, cartItem._id as string)} className = "fa fa-minus-square mx-1 text-success"/>
                                                                       {cartItem.qty}
                                                                       <i onClick = {clickIncrQty.bind(this, cartItem._id as string)} className = "fa fa-plus-square mx-1 text-success"/>
                                                                   </td>
                                                                   <td>&#163;{Number(cartItem.price) * Number(cartItem.qty)}</td>
                                                                   <td>
                                                                       <button onClick={clickDeleteProduct.bind(this, cartItem._id as string)} className = "btn btn-danger btn-sm">
                                                                           <i className = "fa fa-trash"/>
                                                                       </button>
                                                                   </td>
                                                                </tr>
                                                            )
                                                        })
                                                    }
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="card">
                                        <div className="card-header bg-dark text-white">
                                            <p className="h4">Your Total </p>
                                        </div>
                                        <div className="card-body">
                                            <ul className="list-group">
                                                <li className = "list-group-item list-group-item-success">
                                                    Total : <span className="fw-bold">&#163; {calcTotal().toFixed(2)}</span>
                                                </li>
                                                <li className = "list-group-item list-group-item-success">
                                                    Tax : <span className="fw-bold">&#163; {calcTax().toFixed(2)}</span>
                                                </li>
                                                <li className = "list-group-item list-group-item-success">
                                                    Grand Total : <span className="fw-bold">&#163; {calcGrandTotal().toFixed(2)}</span>
                                                </li>
                                            </ul>
                                            <NavLink to = {'/orders/checkout'} className="btn btn-success btn-sm">Checkout</NavLink>
                                            <NavLink to = {'/'} className="btn btn-secondary btn-sm">Shope More</NavLink>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    </React.Fragment> : <React.Fragment>
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <p className="h5"> --------- Cart is Empty --------- </p>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
            }
        </React.Fragment>
    )
};

export default CartPage;