import { User } from '../../modules/users/models/User';
import { UserService } from '../../services/userService';
import * as userActions from './user.actions';
import { LOGOUT_USER } from './user.actions';

export interface UserState{
    user : User;
    loading : boolean;
    errorMessage : string;
    token : string;
    isAuthenticated : boolean;
}

let initialState:UserState = {
    user : {} as User,
    loading : false,
    errorMessage : '',
    token : '',
    isAuthenticated : false
};

export const reducer = (state = initialState, action : any):UserState => {
    switch(action.type){
        //register a User
        case userActions.REGISTER_USER_REQUEST:
            return {
                ...state,
                loading : true
            };
        case userActions.REGISTER_USER_SUCCESS:
            return {
                ...state,
                loading : false
            };
        case userActions.REGISTER_USER_FAILURE:
            return {
                ...state,
                loading : false,
                errorMessage : action.payload.error
            };
        //Login a User
        case userActions.LOGIN_USER_REQUEST:
            return {
                ...state,
                loading : true
            };
        case userActions.LOGIN_USER_SUCCESS:
            UserService.setToken(action.payload.token);
            return {
                ...state,
                loading : false,
                token : action.payload.token,
                isAuthenticated : true
            };
        case userActions.LOGIN_USER_FAILURE:
            return {
                ...state,
                loading : false,
                token : '',
                isAuthenticated : false,
                errorMessage : action.payload.error
            };
        case LOGOUT_USER:
            UserService.removeToken();
            return {
                ...state,
                user  : {} as User,
                loading : false,
                token : '',
                isAuthenticated : false,
            }
        //Get User Information
        case userActions.GET_USER_INFO_REQUEST:
            return {
                ...state
            }
        case userActions.GET_USER_INFO_SUCCESS : 
            return {
                ...state,
                user : action.payload.user,
                isAuthenticated : true
            };
        case userActions.GET_USER_INFO_FAILURE : 
            return {
                ...state,
                user : {} as User,
                isAuthenticated : false,
                token : ''
            };
        //Update shipping Address
        case userActions.UPDATE_ADDRESS_INFO_REQUEST:
            return {
                ...state,
                loading : true
            }
        case userActions.UPDATE_ADDRESS_INFO_SUCCESS : 
            return {
                ...state,
                loading : false,
                user : action.payload.user
            };
        case userActions.UPDATE_ADDRESS_INFO_FAILURE : 
            return {
                ...state,
                loading : false,
                errorMessage : action.payload.error
            };
        default : return state;
    }
};