import { Product } from '../../modules/products/models/Product';
import * as productActions from './product.action';

export interface ProductState{
    selectedProduct: Product;
    products : Product[];
    loading : boolean;
    errorMessage : string;
}

let initialState: ProductState = {
    selectedProduct : {} as Product,
    products : [] as Product[],
    loading : false,
    errorMessage : ''
}

export const reducer = (state:ProductState= initialState, action:any):ProductState => {
    switch(action.type){
        //Upload Product
        case productActions.UPLOAD_PRODUCT_REQUEST:
            return {
                ...state,
                loading : true
            };
        case productActions.UPLOAD_PRODUCT_SUCCESS:
        return {
            ...state,
            loading : false
        };
        case productActions.UPLOAD_PRODUCT_FAILURE:
        return {
            ...state,
            loading : false
        };
        //get all Products
        case productActions.GET_PRODUCTS_REQUEST:
            return {
                ...state,
                loading : true
            };
        case productActions.GET_PRODUCTS_SUCCESS:
        return {
            ...state,
            loading : false,
            products : action.payload.products
        };
        case productActions.GET_PRODUCTS_FAILURE:
        return {
            ...state,
            loading : false
        };
        //get a single Product
        case productActions.GET_PRODUCT_REQUEST:
            return {
                ...state,
                loading : true
            };
        case productActions.GET_PRODUCT_SUCCESS:
            return {
                ...state,
                loading : false,
                selectedProduct : action.payload.product
            };
        case productActions.GET_PRODUCT_FAILURE:
            return {
                ...state,
                loading : false
            };
        default: return state;
    }
}