import React, { useState } from 'react';
import {NavLink, useHistory} from 'react-router-dom';
import * as userActions from '../../redux/users/user.actions';
import {useDispatch} from 'react-redux';

interface IProps{}
interface IState{}

let UserLogin : React.FC<IProps> = ({}) => {
    let history = useHistory();
    let dispatch = useDispatch();

    let [user, setUser] = useState({
        email : '',
        password : ''
    });

    let [userError, setUserError] = useState({
        emailError : '',
        passwordError : ''
    });


let validateEmail = (event : React.ChangeEvent<HTMLInputElement>) => {
    setUser({...user, email : event.target.value});
    let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
    !regExp.test(event.target.value) ? setUserError({...userError, emailError : 'Enter a Valid Email'}) : setUserError({...userError, emailError : ''});
};

let validatePassword = (event : React.ChangeEvent<HTMLInputElement>) => {
setUser({...user, password : event.target.value});
let regExp = /^[a-zA-Z0-9_]\w{5,14}$/;
!regExp.test(event.target.value) ? setUserError({...userError, passwordError : 'Enter a Valid Password!'}) : setUserError({...userError, passwordError : ''});
};

let submitLogin = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    dispatch(userActions.loginUser(user, history));
};


    return(
        <React.Fragment>
        <section className = "mt-3">
            <div className="container">
                <div className="row">
                    <div className="col">
                        <p className="h3 text-success">Login Here</p>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corrupti iste pariatur minima eum, ducimus rerum eos assumenda dolores dicta mollitia consequatur sequi amet libero quia delectus earum temporibus! Id, exercitationem?</p>
                    </div>
                </div>
            </div>
        </section>
        <section className="mt-3">
            <div className="container">
                <div className="row">
                    <div className="col-md-4">
                       <form onSubmit = {submitLogin}>
                       <div className="mb-2">
                               <input 
                               value = {user.email}
                               onChange = {validateEmail}
                               type="email" className={`form-control ${userError.emailError.length > 0 ? 'is-invalid' : ''}`} placeholder= "Email" />
                                {userError.emailError.length > 0 ? <small className="text-danger fw-bold">{userError.emailError}</small> : ''}
                           </div>
                           <div className="mb-2">
                               <input 
                                value = {user.password}
                                onChange = {validatePassword}
                               type="password" className={`form-control ${userError.passwordError.length > 0 ? 'is-invalid' : ''}`} placeholder= "Password" />
                               {userError.passwordError.length > 0 ? <small className="text-danger fw-bold">{userError.passwordError}</small> : ''}
                           </div>
                           <div className="mb-2">
                               <input type="submit" className="btn btn-success btn-sm" value= "Login" />
                           </div>
                           <small>
                               Don't have an Account ?
                               <NavLink to ={'/users/register'} className="text-success fw-bold"> Register</NavLink>
                           </small>
                       </form> 
                    </div>
                </div>
            </div>
        </section>
        </React.Fragment>
    )
};

export default UserLogin;