import * as cartActions from './order.actions';
import {Product} from '../../modules/products/models/Product';
import { ProductState } from '../products/product.reducer';
import { Order } from '../../modules/orders/models/Order';

export interface OrderState{
    loading : boolean;
    cartItems : Product[];
    errorMessage : string;
    orders : Order[];
}

let initialState : OrderState = {
    loading : false,
    cartItems : [] as Product[],
    errorMessage : '',
    orders : [] as Order[]
};

export const reducer = (state  = initialState , action : any): OrderState=> {
    switch(action.type){
        case cartActions.ADD_TO_CART_SUCCESS:
            let existing : Product | undefined = state.cartItems.find(cartItem => cartItem._id === action.payload.product._id);
            if(existing) return state;
            else return {
                ...state,
                cartItems : [...state.cartItems, action.payload.product]
            };
        case cartActions.INCR_PRODUCT_QTY:
            let incrProducts:Product[] = state.cartItems.map(cartItem => {
                if(cartItem._id === action.payload.productId){
                    return{
                        ...cartItem,
                        qty : Number(cartItem.qty) + 1
                    }
                }
                return cartItem ;
            });
            return {
                ...state,
                cartItems : [...incrProducts]
            }
        case cartActions.DECR_PRODUCT_QTY:
            let decrProducts:Product[] = state.cartItems.map(cartItem => {
                if(cartItem._id === action.payload.productId){
                    return{
                        ...cartItem,
                        qty : Number(cartItem.qty) -1 > 0 ? Number(cartItem.qty)- 1 : 1
                    }
                }
                return cartItem ;
            });
            return {
                ...state,
                cartItems : [...decrProducts]
            };
        case cartActions.DELETE_CART_ITEM:
            let remainingItems:Product[] = state.cartItems.filter(cartItem => cartItem._id !== action.payload.productId);
            return{
                ...state,
                cartItems : [...remainingItems]
            }
            //Make payments
        case cartActions.MAKE_STRIPE_PAYMENT_REQUEST:
            return {
                ...state,
                loading : true
            };
        case cartActions.MAKE_STRIPE_PAYMENT_SUCCESS:
            return {
                ...state,
                loading : false
            };
        case cartActions.MAKE_STRIPE_PAYMENT_FAILURE : 
            return {
                ...state,
                loading : false,
                errorMessage : action.payload.error
            };
        //Place Order
        case cartActions.PLACE_ORDER_REQUEST:
            return {
                ...state,
                loading : true
            };
        case cartActions.PLACE_ORDER_SUCCESS:
            return {
                ...state,
                loading : false
            };
        case cartActions.PLACE_ORDER_FAILURE : 
            return {
                ...state,
                loading : false,
                errorMessage : action.payload.error
            };
        //Clear Cart
        case cartActions.CLEAR_CART:
            return {
                ...state,
                cartItems : []
            }
        //Get All orders
        case cartActions.GET_ALL_ORDERS_REQUEST:
            return {
                ...state,
                loading : true
            };
        case cartActions.GET_ALL_ORDERS_SUCCESS:
            return {
                ...state,
                loading : false,
                orders : action.payload.orders
            };
        case cartActions.GET_ALL_ORDERS_FAILURE : 
            return {
                ...state,
                loading : false,
                errorMessage : action.payload.error
            };

        default : return state;
    }
}