import React, { ChangeEvent, useState } from 'react';
import * as userReducer from '../../redux/users/user.reducer';
import {useSelector} from 'react-redux';
import Spinner from '../layout/Spinner';
import {NavLink} from 'react-router-dom';

interface IProps{}
interface IState{
    userKey : userReducer.UserState
}

let UserProfile : React.FC<IProps> = ({}) => {
    let [state, setState] = useState<{enableAddress: boolean}>({
        enableAddress : false
    });

    //get User Details from reduct store
    let userState:userReducer.UserState = useSelector((store : IState) => {
        return store.userKey;
    });

    let {user, loading} = userState;


    return(
        <React.Fragment>
        <section className = "mt-3">
            <div className="container">
                <div className="row">
                    <div className="col">
                        <p className="h3 text-success">Your Profile</p>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corrupti iste pariatur minima eum, ducimus rerum eos assumenda dolores dicta mollitia consequatur sequi amet libero quia delectus earum temporibus! Id, exercitationem?</p>
                    </div>
                </div>
            </div>
        </section>
        {
            loading ? <Spinner/> : 
            <React.Fragment>
                {
                    user && Object.keys(user).length > 0 && 
                    <section className = "mt-3">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-2 text-center">
                                    <img src= {user.avatar} className="img-thumbnail rounded-circle img-fluid" alt="" />
                                    <NavLink to = {'/orders/list'} className = "btn btn-warning btn-sm">Show Previous Order</NavLink>
                                </div>
                                <div className="col-md-8">
                                    <div className="card">
                                        <div className="card-header bg-dark text-white">
                                            <p className="h4">Your Information</p>
                                        </div>
                                        <div className="card-body">
                                            <ul className = "list-group">
                                                <li className = "list-group-item">
                                                    Name : <small className="fw-bold">{user.name}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Email : <small className="fw-bold">{user.email}</small>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="card mt-3">
                                        <div className="card-header bg-dark text-white">
                                            <p className="h4 float-start">Shipping Address</p>
                                            {/* <pre>{JSON.stringify(state.enableAddress)}</pre> */}
                                            <NavLink to = {`/users/profile/${user._id}`} className = "btn btn-danger btn-sm float-end">
                                                <i className = "fa fa-edit"/>
                                            </NavLink>
                                        </div>
                                        <div className="card-body">
                                        {
                                            user.address &&
                                            <ul className = "list-group">
                                                <li className = "list-group-item">
                                                    Flat : <small className="fw-bold">{user.address.flat}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Street : <small className="fw-bold">{user.address.street}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Landmark : <small className="fw-bold">{user.address.landmark}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    City : <small className="fw-bold">{user.address.city}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    State : <small className="fw-bold">{user.address.state}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Country : <small className="fw-bold">{user.address.country}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Pin : <small className="fw-bold">{user.address.pin}</small>
                                                </li>
                                                <li className = "list-group-item">
                                                    Mobile : <small className="fw-bold">{user.address.phone}</small>
                                                </li>
                                            </ul>
                                        }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                }
                <div style = {{marginBottom : '150px'}}></div>
            </React.Fragment>
        }

        </React.Fragment>
    )
};

export default UserProfile;